<template>
     <nav class="BarraNavegacao">
        <RouterLink :to="'/'">
        <img src='Imagens/LogoNicolasASilva.png' alt="" />
        </RouterLink>
        <section>
          <a href="#ArticleSobre">Sobre o Projeto</a>
          <a href="#ArticleHabilidades">Habilidades</a>
          <a href="#ArticleImagens">Imagens</a>
        </section>
    </nav>
    <section class="ConteudoPortifolio">
        <h4>SOBRE O <span>PROJETO</span> </h4>
        <article id="ArticleSobre" class="ArticleSobre_detalhe">
            <div class="InfosPrincipal">
                <h5>NOME: <span>{{ Projeto ? Projeto.Nome : '' }}</span></h5>
                <h5>CODIGO FONTE: <RouterLink :to="Projeto ? Projeto.Codigo_Fonte || '' : ''">CODIGO NO GIT</RouterLink></h5>
                <h5>RESPONSIVO: <span>{{ Projeto ? Projeto.Responsivo : '' }}</span></h5>
                <h5>TIPO: <span>{{ Projeto ? Projeto.Tipo : '' }}</span></h5>
                <h5>COLABORADORES: <span>{{ Projeto ? Projeto.Colaborador : '' }}</span></h5>
            </div>

            <div class="InfosAdicionais_detalhe">
                <div class="CardInfoAdicional">
                    <h2>{{Projeto?Projeto.Duracao[0]:""}}<span>{{Projeto?Projeto.Duracao[1]:""}}</span></h2>
                    <div/>
                    <h3>Tempo de Projeto</h3>
                </div>
            </div>
        <div class="Descricao">{{Projeto?Projeto.Descricao:"Projeto não encontrado, verifique se houve alterações na pesquisa"}}</div>
        </article>

        <h4>MINHAS <span>HABILIDADES</span> </h4>
        <article v-if="Projeto" id="ArticleHabilidades" class="ArticleHabilidades_detalhe">
            <div class="Habilidade_detalhe" :key=Categoria v-for="(Habilits, Categoria) in Projeto.Habilidades">
                <h4>{{Categoria}}</h4>
                <div class="ListaHabilidades">
                    <div :key=Habilidade v-for="(Habilidade) in Habilits">
                        <h5>{{Habilidade}}</h5>
                    </div>
                </div>
            </div>
        </article>

        <h4> <span>IMAGENS</span> </h4>
        <article  v-if="Projeto" id="ArticleImagens" class="ArticleImagens_detalhe">
            <img :src='`${process && process.env.BASE_URL||""}${Imagem}`' :key=Index v-for="(Imagem,Index) in Projeto.Imagens"/>
        </article>
    </section>
</template>
  
<script>
    export default {
        name: 'TemplateComponent',
        data(){
            return {
                Projeto:null,
            }
        },
        methods:{
            async ObterDados(){
                try{
                    await fetch(`${process.env.BASE_URL}Informacoes.json`,{method:"GET"})
                    .then((Response)=>{
                        return Response.json()
                    }).then((data)=>{
                        this.Projeto=data[this.id]
                    })
                }catch(e){
                    console.error("Catch",e);
                }
            }
        },
        mounted(){this.ObterDados()},
        props: {
            id: {
                type: String,
                required: true
            }
        }
    };
</script>
  
<style>@import url("./Style.css")</style>
  